<template>
    <div class="distributor-main" :class="{showSearch:showSearch}">
        <div class="search-box">
            <el-input :placeholder="$L('输入用户名称/姓名')" size="small" v-model="inputValue" class="input-with-select"
                      clearable
                      @keyup.enter.native="$event.target.blur"
                      @change="searchFunc">
                <el-button slot="append" icon="el-icon-search"></el-button>
            </el-input>
            <div class="search-close" @click="doSearch(false)"><i class="el-icon-close"></i></div>
        </div>
        <vue-scroll>
            <div class="main-table">
				<div v-if="routerData.list && routerData.list.length > 0">
					<div v-for="item in routerData.list" class="table-box" >
					    <div class="l-box">
					        <user-avatars :src="item.headPic" class="img"></user-avatars>
					        <p class="p">{{item.userName}}</p>
					        <p class="p">{{$L(item.grade)}}</p>
					    </div>
					    <div class="r-box">
					        <p class="p">
					            <span class="span">{{$L('姓名')}}:</span>
					            <span class="name">{{ item.contact!==''?item.contact:item.userName }}</span>
					        </p>
					        <p class="p">
					            <span class="span">{{$L('手机号')}}:</span>{{item.mobile ? item.mobile : $L('无')}}
					        </p>
					        <p class="p">
					            <span class="span">{{$L('订单总金额')}}:</span><span class="font-red msg">{{item.totalAmount}}</span>
					        </p>
					        <p class="p">
					            <span class="span">{{$L('产生佣金总额')}}:</span><span class="font-red msg">{{item.fullCommission}}</span>
					        </p>
					    </div>
					    <span class="status" :class="statusFunc(item.status).color">{{ statusFunc(item.status).text }}</span>
					    <router-link :to="{name:'distributorDetailsMobile',params:{id:item.distributorId,type:'all'}}">
					        <span class="order mainFontColor mainBorderColor">{{$L('查看详情')}}</span>
					    </router-link>
					</div>
					<!-- 分页 -->
					<div class="list-pages" v-if="routerData.allNum > pageSize">
					    <el-pagination
					            background
					            small
					            @current-change="currentChange"
					            :current-page="currentPage"
					            :page-size="pageSize"
					            layout="prev, pager, next"
					            :total="routerData.allNum">
					    </el-pagination>
					</div>
				</div>
                <div class="no-data" v-else>{{$L('暂无数据')}}</div>
            </div>
        </vue-scroll>
    </div>
</template>

<script>
    import UserAvatars from "../../../components/common/userAvatars";
    export default {
        name: "distributorList",
        components: {UserAvatars},
        props : {

        },
        data () {
            return {
                showSearch: false,
				routerData:{},
				inputValue:'',
				pageSize:10,
				currentPage:1,
            }
        },
        computed:{
            mobileTopBtn: {
                get() {
                    return this.$store.state.mobileTopBtn;
                },
                set(val) {
                    this.$store.commit('setMobileTopBtn', val);
                }
            }
        },
        methods:{
			getData(router) {
			    const _self = this;
			    _self.loading = true;
			    let arr = {
			        p:_self.currentPage,
			        keyword:_self.inputValue?_self.inputValue:"",
			    };
			    _self.$api('distributionList', arr, function (reData) {
			        if (reData.flag === 1) {
			            _self.routerData = reData.data;
			            _self.loading = false;
			        } else if (reData.flag === -1) {
			            //登录超时了
			            _self.$confirm(_self.$L('请重新登录'), _self.$L('登录超时'), {
			                confirmButtonText: _self.$L('重新登录'),
			                cancelButtonText: _self.$L('取消'),
			                type: 'error',
			                distinguishCancelAndClose: true
			            }).then(() => {
			                _self.$router.push({name: 'login', query: {redirect: _self.$route.fullPath}});
			            }).catch(action => {
			                _self.$gotoWebHome();
			            });
			        } else {
			            //未知错误
			            _self.$confirm(_self.$L('数据加载失败'), _self.$L('出错了'), {
			                confirmButtonText: _self.$L('点击重试'),
			                cancelButtonText: _self.$L('返回首页'),
			                type: 'error',
			                distinguishCancelAndClose: true
			            }).then(() => {
			                _self.getData(router);
			            }).catch(action => {
							_self.$router.push({name: 'distributorMain'});
			            });
			        }
			    }, function (error) {
			        //网络请求错误
			        _self.$confirm(_self.$L('网络请求失败'), _self.$L('出错了'), {
			            confirmButtonText: _self.$L('点击重试'),
			            cancelButtonText: _self.$L('返回首页'),
			            type: 'error',
			            distinguishCancelAndClose: true
			        }).then(() => {
			            _self.getData(router);
			        }).catch(action => {
						_self.$router.push({name: 'distributorMain'});
			        });
			    });
			},
            currentChange(val){
                this.currentPage = val;
                this.getData();
            },
			searchFunc(val){
			    this.inputValue = val;
			    this.getData();
			},
            makeTop(){
				this.mobileTopBtn = [
					{
						icon: 'bbx-icon-sousuo',
						func: () => {
							this.doSearch(true);
						}
					},
				];
            },
            doSearch(type){
                if(type){
                    this.showSearch = true;
                }else{
                    this.searchType = "all";
                    this.showSearch = false;
                }
            },
			statusFunc(value){
			    let x;
			    switch(value){
			        case -1:
			            x = {text:this.$L('停用'),color:'font-red'};
			            break;
			        default:
			            x = {text:this.$L('生效'),color:'font-green'};
			            break;
			    }
			    return x;
			},
        },
        created() {
            this.makeTop();
			this.getData();
        },
        beforeRouteUpdate(to, from, next) {
            // 在当前路由改变，但是该组件被复用时调用
            // 举例来说，对于一个带有动态参数的路径 /foo/:id，在 /foo/1 和 /foo/2 之间跳转的时候，
            // 由于会渲染同样的 Foo 组件，因此组件实例会被复用。而这个钩子就会在这个情况下被调用。
            // 可以访问组件实例 `this`
            this.makeTop();
            next();
        },
        beforeDestroy() {
            //销毁当前页面时，必须清空顶部按钮，不然会影响其他页面
            this.mobileTopBtn = null
        },
    }
</script>

<style lang="scss" scoped>
    @import "../../../assets/css/common/lib.scss";
    .distributor-main{
        .search-box{
            position: absolute;
            width: 100%;
            height: 45px;
            line-height: 45px;
            background-color: #fff;
            top: -45px;
            left: 0;
            z-index: 100;
            border-bottom: 1px solid #E4E7ED;
            @include diy-animation-show(top);
            .input-with-select{
                width: 260px;
                margin-left: 10px;
            }
            .search-close{
                width: 45px;
                height: 45px;
                text-align: center;
                line-height: 45px;
                position: absolute;
                top: 0;
                right: 0;
                z-index: 100;
                font-size: 18px;
                color: #909399;
                &:active{
                    color: #606266;
                }
            }
        }
        .main-table{
            position: relative;
            z-index: 0;
            @include diy-animation-show(margin);

            .no-data{
                position: relative;
                width: 100%;
                height: 170px;
                line-height: 170px;
                text-align: center;
                background-color: #ffffff;
            }

            .table-box{
                position: relative;
                overflow: hidden;
                padding: 20px 15px;
                background: #fff;
                height: 200px;
                margin-bottom: 10px;
                .l-box{
                    float: left;
                    width: 90px;
                    text-align: center;
                    margin-right: 10px;
                    .img{
                        width: 65px;
                        height: 65px;
                        border-radius: 50%;
                    }
                    .p{
                        line-height: 2;
                    }
                }
                .r-box{
					width: calc(100% - 100px);
                    float: left;
                    .p{
                        line-height: 2;
                        color: #606266;
                        .span{
                            display: inline-block;
                            width: 85px;
                            text-align: right;
                            padding-right: 5px;
                            color: #303133;
                        }
						.name{
							vertical-align: bottom;
							display: inline-block;
							width: 70px;
							overflow: hidden;
							white-space: nowrap;
							text-overflow: ellipsis;
						}
                    }
                }
				.msg{
					overflow: hidden;
					white-space: nowrap;
					text-overflow: ellipsis;
				}
                .font-red {
                    color: #F56C6C;
                }
                .font-green {
                    color: #67C23A;
                }
                .font-warning {
                    color: #E6A23C;
                }
                .status{
                    position: absolute;
                    top: 22px;
                    right: 20px;
                }
                .order{
                    position: absolute;
                    right: 20px;
                    bottom: 20px;
                    display: inline-block;
                    width: 90px;
                    height: 30px;
                    line-height: 30px;
                    text-align: center;
                    border: 1px solid;
                    border-radius: 20px;
                }
            }

            &.member{
                .table-box{
                    height: 170px;
                }
            }

            .list-pages{
                text-align: center;
                padding: 10px 0;
            }
        }

        &.showSearch{
            .search-box{
                top: 0;
            }
            .main-table{
                margin-top: 45px;
            }
        }
    }
</style>